$purple: #523dc5;
$black: #38393d;
$blackLight: #4b4d53;
$white: #fff;
$yellow: #F7EB49;
$yellowLight: #faf78e;

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
    font-family: 'DM Sans', sans-serif;
    font-weight: 400;
    font-size: 1rem;
    color: $black;
    padding-top: calc(0.5rem + 117px);
    min-height: 100vh;
    
    @media (max-width: 568px) {
         padding-top: calc(0.5rem + 154px);
    }
}

a {
    text-decoration: none
}

header,
section,
footer {
   
}

.container {
	padding-left: 1rem;
    padding-right: 1rem;
    width: 90vw;
    max-width: 1024px;
    margin: 0 auto;
}

footer {
	padding-top: 3rem;
    padding-bottom: 3rem;
}

