.figure {
    border-radius: 2rem;
    padding: 3rem;
    width: 100%;
    display: block;
    
    &.figure--vivun {
        display: flex;
        justify-content: center;
        background: #13153c;

        svg {
            width: 100%;
            max-width: 460px;
            height: auto;
            display: block;
        }
    }
        
    &.figure--couch {
            position: relative;
            width: 100%;
            height: 0;
            padding-bottom: 50%;
            overflow: hidden;
            background: #645EE6;
            background: -webkit-radial-gradient(top left, #645EE6, #28D3C7);
            background: -moz-radial-gradient(top left, #645EE6, #28D3C7);
            background: radial-gradient(to bottom right, #645EE6, #28D3C7);

            div {
                max-width: 420px;
                overflow: hidden;
                border-radius: 0.25rem;
            }

            img {
               width: 100%;
            }
    }
    
    &.figure--cnn-politics-quiz {
            position: relative;
            width: 100%;
            height: 0;
            padding-bottom: 50%;
            overflow: hidden;
            background: #cc0100;
            background: -webkit-radial-gradient(top left, #cc0100, #0064e1);
            background: -moz-radial-gradient(top left, #cc0100, #0064e1);
            background: radial-gradient(to bottom right, #cc0100, #0064e1);

            div {
                width: 100%;
                position: absolute;
                right: 3rem;
                top: 3rem;
                max-width: 420px;
                overflow: hidden;
                border-radius: 0.25rem;
            }

            img {
                width: 100%;
            }
    }
}