
.case-studies {
   
    .case-study {
        padding-top: 3rem;

        a {
            transition: top ease-in-out 250ms;
            top: 0;
            position: relative;
            display: block;
            color: $black;
        }

        .figure {
            transition: opacity ease-in-out 250ms;
            opacity: 1; 
        }
        
        .figcaption {
            padding: 1rem 3rem;
            
            @media (max-width: 568px){
                padding: 1rem 0;
            }

             h3 {
                font-size: 2rem;
               
                max-width: 640px;
                font-weight: 400;
                padding-bottom: 1rem;
                
                 @media (max-width: 568px){
                     font-size: 1.5rem;
                    padding-right: 0;
                }
            }

            p {
                span {
                    color: $blackLight;
                }
            }
        }

        &:hover {
            a {
                top: -1.5rem;
            }

            figure {
                opacity: 0.75;
            }
        }
        
    }
    
}