.case-study--preview {
    .container--hero {
        display: flex;
        flex-direction: column-reverse;
    }
    
    .container--overview,
    .container--details {
         padding: 0 3rem;
        
        @media (max-width: 568px){
             padding: 0 1.5rem;
        }
        
        h4 {
            padding: 0 0 1rem 0;
            font-size: 1rem;
            font-weight: 700;
            max-width: 640px;
        }
    }
    
    .container--overview {
        padding-top: 3rem;
        
        p {
            font-size: 2rem;
            max-width: 640px;
            @media (max-width: 568px){
                font-size: 1.5rem;
            }
        }
    }
    
    .container--details {
        display: flex;
        gap: 6rem;
        padding-top: 3rem;
        @media (max-width: 568px){
            flex-direction: column;
            gap: 3rem;
        }
    }
    
    
    .container--role,
    .container--team {
        width: 50%;
        @media (max-width: 568px){
            width: 100%;
        }
        
        p {
            line-height: 1.5;
        }
        
        p + p {
           padding-top: 1rem; 
        }
    }
    
    
}