.masthead {
    padding-top: 3rem;
    padding-bottom: 3rem;
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    width: 100%;
    transition: top ease-in-out 250ms;
    background: rgba(255, 255, 255, 0.95);
    
    &.hide {
        top: -117px;
    }
    
    .container {
        display: flex;
        justify-content: space-between;
    }
    
    h1 {
        font-weight: 700;
        font-size: 1rem;
    }
    
    a {
        font-size: 1rem;
        color: $blackLight;
        transition: color ease-in-out 250ms, box-shadow ease-in-out 250ms;
        box-shadow: inset 0 0 0 0 $yellowLight;
        margin: 0 -0.25rem;
        padding: 0 0.25rem;

        &.active {
            color: $black;
            font-weight: 700;
        }
        
        &.strike {
            text-decoration: line-through;
            pointer-events: none;
        }

        &:hover {
            box-shadow: inset 25rem 0 0 0 $yellowLight;
        }
    }
    
    nav {
        display: flex;
        gap: 1rem;
    }
    
    @media (max-width: 568px){
        .container {
            flex-direction: column;
            justify-content: space-between;
            gap: 1rem;
        }
        
        &.hide {
            top: -154px;
        }
    }
}
